import { cloneTemplate } from "../../utils/utils";
import { Basket } from "./abstracts/Basket";
export class BasketModal extends Basket {
    constructor(events) {
        super(cloneTemplate('#basket'), {
            onBuy: () => events.emit('busket:buy')
        });
        this.events = events;
        this.isDisabled = true;
    }
}
